*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  margin: 0;
}

input {
  outline: none;
  border: none;
  background-image: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  top: auto;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --font-josefin-sans: "Josefin Sans", sans-serif;
  --font-weight-default: 400;
  --font-weight-light: 300;
  --font-weight-bold: 600;
}

body {
  font-family: var(--font-josefin-sans);
  font-weight: var(--font-weight-default);
  color: var(--main);
}

h1,
p {
  margin-top: 0;
}

h1 {
  margin-bottom: 1.25rem;
  line-height: 1.1;
  font-weight: var(--font-weight-bold);
  font-size: 2.5rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 3rem;
  }
}

p {
  margin-bottom: 1.875rem;
  line-height: 1.5;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --main: hsl(0, 36%, 70%);
  --accent: hsl(0, 93%, 68%);
  --black: hsl(0, 6%, 24%);
  --gradient-main-start: 0, 80%, 86%;
  --gradient-main-end: 0, 74%, 74%;
  --gradient-filter-start: 0, 0%, 100%;
  --gradient-filter-end: 0, 0%, 1%;
  --gradient-main: linear-gradient(
    135deg,
    hsl(var(--gradient-main-start)),
    hsl(var(--gradient-main-end))
  );
  --gradient-filter: linear-gradient(
    135deg,
    hsla(var(--gradient-filter-start), 0.5),
    hsla(var(--gradient-filter-end), 0.05)
  );
  --gradient-hover: linear-gradient(
    135deg,
    hsla(var(--gradient-main-start), 1),
    hsla(var(--gradient-main-start), 1)
  );
}

.content {
  margin: 0 2rem;
}
@media (min-width: 40em) {
  .content {
    margin: 0;
  }
}

.grid-main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, auto);
  column-gap: 1rem;
}
@media (min-width: 40em) {
  .grid-main {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 0;
  }
}

.header {
  grid-column: 1/2;
  grid-row: 1/2;
}
@media (min-width: 40em) {
  .header {
    grid-column: 2/3;
    z-index: 1;
  }
}

.main {
  grid-column: 1/2;
  grid-row: 2/end;
  z-index: 0;
}
@media (min-width: 40em) {
  .main {
    grid-column: 1/end;
    grid-row: 1/end;
  }
  .main .coming {
    grid-column: 1/5;
  }
}

.grid-2nd {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}
@media (min-width: 40em) {
  .grid-2nd {
    grid-template-rows: repeat(6, 1fr);
  }
}
.grid-2nd .coming__title {
  grid-column: 1/end;
  grid-row: 1/2;
}
@media (min-width: 40em) {
  .grid-2nd .coming__title {
    grid-column: 3/6;
    grid-row: 2/3;
    align-self: end;
  }
}
@media (min-width: 71.875em) {
  .grid-2nd .coming__title {
    grid-row: 2/4;
  }
}
.grid-2nd .coming__text {
  grid-column: 1/end;
  grid-row: 2/3;
}
@media (min-width: 40em) {
  .grid-2nd .coming__text {
    grid-column: 3/7;
    grid-row: 3/4;
  }
}
@media (min-width: 71.875em) {
  .grid-2nd .coming__text {
    grid-row: 4/5;
  }
}
.grid-2nd .coming__form {
  grid-column: 1/end;
  grid-row: 3/4;
  align-items: start;
}
@media (min-width: 40em) {
  .grid-2nd .coming__form {
    grid-column: 3/7;
    grid-row: 4/5;
  }
}
@media (min-width: 71.875em) {
  .grid-2nd .coming__form {
    grid-row: 5/6;
    align-self: end;
  }
}

.coming__img {
  grid-column: 1/2;
  grid-row: 1/2;
}
@media (min-width: 40em) {
  .coming__img {
    grid-column: 5/end;
    grid-row: 1/end;
  }
}

.footer {
  grid-column: 1/2;
  z-index: 1;
}
@media (min-width: 40em) {
  .footer {
    grid-column: 1/5;
    grid-row: 4/5;
  }
}

.header {
  padding: 1.5rem 0;
}
@media (min-width: 40em) {
  .header {
    padding: 2.1875rem 0;
  }
}
@media (min-width: 71.875em) {
  .header {
    padding: 3.125rem 0;
  }
}

.header__logo {
  height: 1.125rem;
}
@media (min-width: 40em) {
  .header__logo {
    height: 2.1875rem;
  }
}

.coming {
  padding: 4rem 0;
  text-align: center;
}
@media (min-width: 40em) {
  .coming {
    padding: 0;
    height: 100vh;
    background: url("../../../images/bg-pattern-desktop.svg") white center;
    background-size: cover;
    text-align: left;
  }
}

.coming__title {
  text-transform: uppercase;
  color: var(--black);
  letter-spacing: 0.625rem;
}
@media (min-width: 40em) {
  .coming__title {
    max-width: 70%;
  }
}
.coming__title span {
  font-weight: var(--font-weight-light);
  color: var(--main);
}

.coming__form {
  display: grid;
  grid-template-rows: repeat(2, auto);
  grid-template-columns: repeat(2, auto);
}
.coming__form input[type=email] {
  grid-row: 1/2;
  grid-column: 1/3;
  z-index: 0;
  border: 1px var(--main) solid;
  border-radius: 1.875rem;
  padding: 0.9375rem 0 0.9375rem 1.25rem;
  color: var(--main);
}
.coming__form input[type=email]:focus, .coming__form input[type=email]:not(:placeholder-shown) {
  color: var(--black);
}
.coming__form button {
  z-index: 1;
  grid-row: 1/2;
  grid-column: 2/3;
  justify-self: end;
  position: relative;
  border-radius: 1.875rem;
  width: 4rem;
  height: 2.875rem;
  background: var(--gradient-main);
  box-shadow: 0px 7px 10px hsla(var(--gradient-main-start), 0.6);
  transition: background 150ms ease-in-out;
}
@media (min-width: 40em) {
  .coming__form button {
    width: 5rem;
  }
}
@media (min-width: 71.875em) {
  .coming__form button {
    width: 6.25rem;
  }
}
.coming__form button:hover {
  background: var(--gradient-filter), var(--gradient-hover);
}
.coming__form button img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.coming__form .coming__msg {
  grid-column: 1/3;
  grid-row: 2/3;
  height: 1.125rem;
  margin-top: 0.5rem;
  padding-left: 1.25rem;
  text-align: left;
  font-size: 0.875rem;
}
.coming__form .coming__msg--error {
  color: var(--accent);
}
.coming__form .coming__msg--success, .coming__form .coming__msg--info {
  color: var(--black);
}

.coming__img {
  width: 100%;
  height: 17.5rem;
  overflow: hidden;
}
@media (min-width: 40em) {
  .coming__img {
    height: 100vh;
    overflow: auto;
  }
}
.coming__img img {
  width: 100%;
  margin-top: -2.625rem;
}
@media (min-width: 40em) {
  .coming__img img {
    margin-top: 0;
    height: 100%;
    object-fit: cover;
  }
}

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 * 3. Show the overflow in Edge.
 * 4. Remove the inheritance of text transform in Firefox.
 */
button {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
  overflow: visible; /* 3 */
  text-transform: none; /* 4 */
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button {
  cursor: pointer;
  border: none;
}

.footer .attribution {
  margin-bottom: 0.375rem;
  font-size: 0.6875rem;
  text-align: center;
  color: var(--black);
}
.footer a {
  color: var(--accent);
}